:root {
  --bright-orange-color: #e38826;
  --dark-cyan-color: #006970;
  --very-dark-cyan-color: #004241;
  --transparent-white-color: #ffffffbf;
  --very-light-gray-color: #f2f2f2;
  --white-color: #fff;
  --grid-container-width: 20.4375rem;
  --grid-container-height: 82.875rem;
  --heading-size: 2.5rem;
  --heading-line: 1.2;
  --paragraph-size: .9375rem;
  --paragraph-line: 1.67;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  font-family: Lexend Deca, sans-serif;
  font-size: var(--paragraph-size);
  line-height: var(--paragraph-line);
  background-color: var(--very-light-gray-color);
  margin: 0;
  padding: 0;
}

h1, h2 {
  font-family: Big Shoulders Display, cursive;
  font-size: var(--heading-size);
  line-height: var(--heading-line);
  color: var(--very-light-gray-color);
  margin: 0;
  padding: 0;
  font-weight: 700;
}

a, p {
  font-family: inherit;
  line-height: inherit;
  margin: 0;
  padding: 0;
  font-weight: 400;
}

a {
  text-decoration: none;
}

p {
  color: var(--transparent-white-color);
}

.btn {
  background: var(--very-light-gray-color);
  border-radius: 1.5625rem;
  justify-content: center;
  align-items: center;
  width: 9.125rem;
  height: 3rem;
  display: inline-flex;
}

.btn:hover {
  color: var(--white-color);
  border: 2px solid var(--white-color);
  background: none;
}

.btn--orange {
  color: var(--bright-orange-color);
}

.btn-darkcyan {
  color: var(--dark-cyan-color);
}

.btn-darkercyan {
  color: var(--very-dark-cyan-color);
}

.grid {
  width: var(--grid-container-width);
  height: var(--grid-container-height);
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: 1fr;
  display: grid;
}

.card {
  flex-direction: column;
  padding: 3rem;
  display: flex;
}

.card:first-child {
  border-radius: .5rem .5rem 0 0;
}

.card:last-child {
  border-radius: 0 0 .5rem .5rem;
}

.card__heading {
  text-transform: uppercase;
}

.card__icon {
  flex-basis: 4.6875rem;
}

.card__heading {
  flex-basis: 4.5625rem;
}

.card__description {
  flex-basis: 9.375rem;
}

.card--orange {
  background-color: var(--bright-orange-color);
}

.card--darkcyan {
  background-color: var(--dark-cyan-color);
}

.card--darkercyan {
  background-color: var(--very-dark-cyan-color);
}

.centered {
  margin: 0 auto;
}

header {
  text-align: center;
}

header h1 {
  color: var(--dark-cyan-color);
}

.attribution {
  color: var(--dark-cyan-color);
  margin: 1rem 0;
}

.attribution a {
  color: var(--bright-orange-color);
  text-decoration: none;
}

.attribution a:hover {
  text-decoration: underline;
}

@media screen and (width >= 90em) {
  :root {
    --grid-container-width: 57.5rem;
    --grid-container-height: 31.25rem;
  }

  .grid {
    width: var(--grid-container-width);
    height: var(--grid-container-height);
    grid-template-rows: 1fr;
    grid-template-columns: repeat(3, 1fr);
  }

  .card:first-child {
    border-radius: .5rem 0 0 .5rem;
  }

  .card:last-child {
    border-radius: 0 .5rem .5rem 0;
  }

  .card__description {
    flex-basis: 13rem;
  }

  .centered {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .attribution {
    margin: 0;
  }
}

